export default {
	data() {
		return {
			isLoaing: true,
			navs: ['待发货', '已完成'],
			currentIndex: 0,
			orders: [],
			page: 1,
			isNull: false,
			type: 1,
			stopLoad: false,
			takeIndex: -1,
			modelName: ''
		}
	},
	onLoad() {
		this.getOrderList(this.type)
	},
	methods: {
		showModel(index) {
			this.takeIndex = index
			this.modelName = 'takeModel'
		},
		closeModel() {
			this.modelName = ''
		},
		//确认收货
		sureTake() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/receiveScoreGoods",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					order_id: this.orders[this.takeIndex].point_orderid
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.closeModel()
					if (res.data.msg == '操作成功') {
						this.orders.splice(this.takeIndex, 1)
						uni.showToast({
							title: '已确认收货',
							icon: 'success'
						})
						this.isNull = this.stopLoad = false
						this.page = 1
						this.getOrderList()
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		chooseOptions(index) {
			this.page = 1
			this.currentIndex = index
			this.isLoaing = true
			this.isNull = this.stopLoad = false
			switch (index) {
				case 0:
				this.type = 1
					break;
				case 1:
				this.type = 3
					break;
				default:
					break;
			}
			this.getOrderList()
		},
		//获取订单列表
		getOrderList() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/scoreOrderList",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					type: this.type,
					page: 1,
					offset: 6
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.orders = res.data.data.data
						this.isLoaing = false
						if (this.orders.length == 0) {
							this.isNull = true
						}
					}
				}
			})
		},
		loadMore() {
			if (this.stopLoad) {
				return
			}
			this.page++
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/scoreOrderList",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					type: this.type,
					page: this.page,
					offset: 6
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.data.length == 0) {
							this.stopLoad = true
							return
						}
						for (var i = 0; i < res.data.data.data.length; i++) {
							this.orders.push(res.data.data.data[i])
						}
					}
				}
			})
		},
		//跳转查看订单详情
		toOrderDetail(orderid) {
			uni.navigateTo({
				url: "../exchangeOrderDetail/exchangeOrderDetail?orderid=" + orderid
			})
		}
	},
	onReachBottom() {
		this.loadMore()
	}
}
